/* make variables */
:root {
  --lightgray: #D9D9D9;
  --gray: #9c9c9c;
  --orange: #f48915;
  --darkGrey: #464D53;
  --caloryCard: #656565;
  --planCard: linear-gradient(210.41deg, #fa5042 1.14%, #ffa739 100.75%);
  --appColor:  #3c3f45;
}

.App {
  background-image: url(data:image/png;base64,);
  mix-blend-mode: overlay;
  background-color: var(--appColor);
  display: flex;
  flex-direction: column;
  gap: 6rem;
  overflow: hidden;
}
::-webkit-scrollbar{
  display: none;
}
.stroke-text{
  color: transparent !important;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: wheat;
  font-family: Arial, Helvetica, sans-serif;

}

element.style {
}
.hero-blur {
    height: 30rem;
    left: 0;
    width: 22rem;
}

.blur {
    background: rgba(253,120,43,.69);
    border-radius: 50%;
    -webkit-filter: blur(400px);
    filter: blur(220px) !important;
    position: absolute;
    z-index: -9;
}
